import { Component, OnInit } from '@angular/core';
import { PaladionService } from '../app.service';
import {ActivatedRoute} from "@angular/router";
import { DomSanitizer, SafeHtml } from '@angular/platform-browser';
@Component({
  selector: 'app-vm-mdr-detail',
  templateUrl: './vm-mdr-detail.component.html',
  styleUrls: ['./vm-mdr-detail.component.css']
})
export class VmMdrDetailComponent implements OnInit {

  mdr:any;
  mdrId:any;
  imgurl:any;
  
  constructor(private sanitizer: DomSanitizer, private paladionService: PaladionService, private route: ActivatedRoute) { 
    this.route.params.subscribe( params => this.mdrId=params.id);
  }

  ngOnInit() {
    this.getMDRLogs();
  }
  trustHTML(html){
    return this.sanitizer.bypassSecurityTrustHtml(html)
   }
  getMDRLogs() {
    this.paladionService.getMdrLogsById(this.mdrId).subscribe(data => {
      this.mdr = data.data;
      if(this.mdr){
        this.paladionService.getTypeById(this.mdr.type_id).subscribe(data => {
          this.imgurl=data.data.type_image;
        })
      }
    })
  }

  copyUrl(){
    let copyText = null; 
     copyText.value = window.location.href;

    /* Select the text field */
    copyText.select();
  
    /* Copy the text inside the text field */
    document.execCommand("copy");
  
    /* Alert the copied text */
    alert("Copied the text: " + copyText.value);
  }

}
