import { Component, OnInit } from '@angular/core';
import { PaladionService } from '../app.service';
import { ActivatedRoute } from '@angular/router'; 

@Component({
  selector: 'app-vm-mdr',
  templateUrl: './vm-mdr.component.html',
  styleUrls: ['./vm-mdr.component.css']
})
export class VmMdrComponent implements OnInit {

  allMdrs:any;
  typeId:any;
  imgurl:any;

  constructor(private paladionService: PaladionService, private route: ActivatedRoute) { 
   this.route.params.subscribe(params => this.typeId=params.id);
  }

  ngOnInit() {
    this.getTypeImage();
    this.getAllMdrLogs();
  }

  getTypeImage(){
    this.paladionService.getTypeById(this.typeId).subscribe(data => {
      this.imgurl=data.data.type_image;
    })
  }

  getAllMdrLogs() {
    this.paladionService.getMdrByTypeId(this.typeId).subscribe(data => {
      this.allMdrs = data.data;
    })
  }

}
