import { Component, OnInit } from '@angular/core';
import { PaladionService } from '../app.service';
import {ActivatedRoute} from "@angular/router";

@Component({
  selector: 'app-page',
  templateUrl: './page.component.html',
  styleUrls: ['./page.component.css']
})
export class PageComponent implements OnInit {

  page:any;
  pageId:any;

  constructor(private paladionService: PaladionService, private route: ActivatedRoute) { 
    this.route.params.subscribe( params => this.pageId=params.id);
  }

  ngOnInit() {
    this.getPage();
  }

  getPage() {
    this.paladionService.getPageById(this.pageId).subscribe(data => {
      this.page = data.data;
    })
  }

}
