import { Component, OnInit } from '@angular/core';
import { PaladionService } from '../app.service';
import {ActivatedRoute} from "@angular/router";

@Component({
  selector: 'app-mdr-detail',
  templateUrl: './mdr-detail.component.html',
  styleUrls: ['./mdr-detail.component.css']
})
export class MdrDetailComponent implements OnInit {

  mdr:any;
  mdrId:any;

  constructor(private paladionService: PaladionService, private route: ActivatedRoute) { 
    this.route.params.subscribe( params => this.mdrId=params.id);
  }

  ngOnInit() {
    this.getMDRLogs();
  }

  getMDRLogs() {
    this.paladionService.getMdrLogsById(this.mdrId).subscribe(data => {
      this.mdr = data.data;
    })
  }

}
