import { Component, OnInit } from '@angular/core';
import { PaladionService } from '../app.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-tm-cat',
  templateUrl: './tm-cat.component.html',
  styleUrls: ['./tm-cat.component.css']
})
export class TmCatComponent implements OnInit {

  allCat: any;
  
    constructor(private paladionService: PaladionService, private router:Router) {
      this.getAllCat();
     }

  
     getAllCat() {
      this.paladionService.getCategoryByMdrTypeId('1').subscribe(data => {
        this.allCat = data.data;
      })
    }

  navigateToMdr(id){
    this.router.navigateByUrl('/tm/mdr/'+id);
  }

  ngOnInit() {
  }

}
