import { Component, OnInit } from '@angular/core';
import { PaladionService } from '../app.service';
import {ActivatedRoute} from "@angular/router";

import { DomSanitizer, SafeHtml } from '@angular/platform-browser';
@Component({
  selector: 'app-tm-mdr-detail',
  templateUrl: './tm-mdr-detail.component.html',
  styleUrls: ['./tm-mdr-detail.component.css']
})
export class TmMdrDetailComponent implements OnInit {

  mdr:any;
  mdrId:any;
  imgurl:any;

  isAdmin:boolean;
  constructor(private sanitizer: DomSanitizer,private paladionService: PaladionService, private route: ActivatedRoute) { 
    this.route.params.subscribe( params => this.mdrId=params.id);
  }

  ngOnInit() {
    this.getMDRLogs();
    this.isAdmin = this.paladionService.isAdmin();
  }

  trustHTML(html){
    return this.sanitizer.bypassSecurityTrustHtml(html)
   }

  getMDRLogs() {
    this.paladionService.getMdrLogsById(this.mdrId).subscribe(data => {
      this.mdr = data.data;
      if(this.mdr){
        this.paladionService.getTypeById(this.mdr.type_id).subscribe(data => {
          this.imgurl=data.data.type_image;
        })
      }
    })
  }

  copyUrl(){
    
    let selBox = document.createElement('textarea');
    selBox.style.position = 'fixed';
    selBox.style.left = '0';
    selBox.style.top = '0';
    selBox.style.opacity = '0';
    selBox.value = window.location.href;
    document.body.appendChild(selBox);
    selBox.focus();
    selBox.select();
    document.execCommand('copy');
    document.body.removeChild(selBox);
    /* Alert the copied text */
    console.log(selBox.value);

  }

}
