import { Component, OnInit } from '@angular/core';
import { PaladionService } from '../app.service';
import {ActivatedRoute} from "@angular/router";
import { DomSanitizer, SafeHtml } from '@angular/platform-browser';

@Component({
  selector: 'app-tm-page',
  templateUrl: './tm-page.component.html',
  styleUrls: ['./tm-page.component.css']
})
export class TmPageComponent implements OnInit {

  page:any;
  pageId:any;
  isAdmin:boolean;
  mdrId:any;

  constructor(private sanitizer: DomSanitizer,private paladionService: PaladionService, private route: ActivatedRoute) { 
    this.route.params.subscribe( params => this.pageId=params.id);
  }

  ngOnInit() {
    
    this.getPage();
    this.isAdmin = this.paladionService.isAdmin();
  }

  
  getPage() {
    this.paladionService.getPageById(this.pageId).subscribe(data => {
      this.page = data.data;
      
    })
  }
  trustHTML(html){
    return this.sanitizer.bypassSecurityTrustHtml(html)
   }


  copyUrl(){
    
    let selBox = document.createElement('textarea');
    selBox.style.position = 'fixed';
    selBox.style.left = '0';
    selBox.style.top = '0';
    selBox.style.opacity = '0';
    selBox.value = window.location.href;
    document.body.appendChild(selBox);
    selBox.focus();
    selBox.select();
    document.execCommand('copy');
    document.body.removeChild(selBox);
    /* Alert the copied text */
    console.log(selBox.value);

  }

}
