import { Component, OnInit } from '@angular/core';
import { DomSanitizer, SafeHtml } from '@angular/platform-browser';
import { PaladionService } from '../app.service';

@Component({
  selector: 'app-tm-faq-detail',
  templateUrl: './tm-faq-detail.component.html',
  styleUrls: ['./tm-faq-detail.component.css']
})
export class TmFaqDetailComponent implements OnInit {

  allFaqs: any;
  mdrId:any;
  isAdmin:boolean;

  

  constructor(private sanitizer: DomSanitizer,private paladionService: PaladionService) { 
    this.mdrId=1;



  }
  trustHTML(html){
    return this.sanitizer.bypassSecurityTrustHtml(html)
   }
   getFaqByMdrTypeId(){
    this.paladionService.getFaqByMdrTypeId(this.mdrId).subscribe(data => {
      this.allFaqs = data.data;
    }) 
  }
  ngOnInit() {
    this.isAdmin = this.paladionService.isAdmin();

    if(this.mdrId){
      this.getFaqByMdrTypeId()
     }
  }



  copyUrl(){
    
    let selBox = document.createElement('textarea');
    selBox.style.position = 'fixed';
    selBox.style.left = '0';
    selBox.style.top = '0';
    selBox.style.opacity = '0';
    selBox.value = window.location.href;
    document.body.appendChild(selBox);
    selBox.focus();
    selBox.select();
    document.execCommand('copy');
    document.body.removeChild(selBox);
    /* Alert the copied text */
    console.log(selBox.value);

  }

}
