import { Component, OnInit } from '@angular/core';
import { PaladionService } from '../app.service';

import { DomSanitizer, SafeHtml } from '@angular/platform-browser';
@Component({
  selector: 'app-tm-faq',
  templateUrl: './tm-faq.component.html',
  styleUrls: ['./tm-faq.component.css']
})
export class TmFaqComponent implements OnInit {
  acc: any;
  allFaqs: any;
  isAdmin:boolean;
  mdrId:any;
  
    constructor(private sanitizer: DomSanitizer,private paladionService: PaladionService) {
      
      //this.getAllFaqs();
     }
  
     trustHTML(html){
      return this.sanitizer.bypassSecurityTrustHtml(html)
     }

     toggleMe(event) {
   
      event.path[0].classList.toggle("active");
      var panel = event.path[0].nextElementSibling;
      if (panel.style.maxHeight){
        panel.style.maxHeight = null;
      } else {
        panel.style.maxHeight = panel.scrollHeight + "px";
      } 
  
     }
  
     getAllFaqs() {
      this.paladionService.getAllFaqsBasedOnMdrId('1').subscribe(data => {
        this.allFaqs = data.data;
        console.log(this.allFaqs);
      })
    }
  
  
  
    ngOnInit() {

      this.isAdmin = this.paladionService.isAdmin();
      
        this.getAllFaqs();
      
      
      
    }


    copyUrl(){
    
      let selBox = document.createElement('textarea');
      selBox.style.position = 'fixed';
      selBox.style.left = '0';
      selBox.style.top = '0';
      selBox.style.opacity = '0';
      selBox.value = window.location.href;
      document.body.appendChild(selBox);
      selBox.focus();
      selBox.select();
      document.execCommand('copy');
      document.body.removeChild(selBox);
      /* Alert the copied text */
      console.log(selBox.value);
  
    }
  

}
